import { createSlice } from "@reduxjs/toolkit";
import { createAsyncThunk } from "@reduxjs/toolkit";
import axios from "axios";
import moment from "moment";

export const fetchWeatherApi = createAsyncThunk(
  "weather/fetchResponse", // !  Prefix , thunk function => the returned value goes to the case fulfilled
  async () => {
    let longLat = { lat: 29.93, lon: 30.94 };
    const response = await axios.get(
      `https://api.openweathermap.org/data/2.5/weather?lat=${longLat.lat}&lon=${longLat.lon}&appid=aad9319522f19b3d2855df285e337a3f`
    );
    let place = response.data.name.replace("Governorate", "");
    let description = response.data.weather[0].description;
    let icon = `https://openweathermap.org/img/wn/${response.data.weather[0].icon}@2x.png`;
    let minTemp = parseFloat((response.data.main.temp_min - 273.15).toFixed(1));
    let maxTemp = parseFloat((response.data.main.temp_max - 273.15).toFixed(1));
    let weatherTemp = parseFloat((response.data.main.temp - 273.15).toFixed(1));
    return { place, description, icon, minTemp, maxTemp, weatherTemp };
    //todo: return this object to actions in extraReducers case fulfilled
  }
);

const initialState = {
  weatherObj: {},
  isLoading: false,
};

export const weatherSlice = createSlice({
  name: "weather",
  initialState,
  reducers: {},
  extraReducers(builder) {
    builder
      .addCase(fetchWeatherApi.pending, (state, action) => {
        state.isLoading = true;
      })
      .addCase(fetchWeatherApi.fulfilled, (state, action) => {
        state.weatherObj = action.payload;
        state.isLoading = false;
      })
      .addCase(fetchWeatherApi.rejected, (state, action) => {
        state.isLoading = true;
      });
  },
});

// Action creators are generated for each case reducer function
export const { increment, decrement, incrementByAmount } = weatherSlice.actions;

export default weatherSlice.reducer;
