import { Typography } from "@mui/material";
import Button from "@mui/material/Button";
import "../App.css";
import { useEffect, useState } from "react";
import moment from "moment";
import { useTranslation } from "react-i18next";
import "moment/min/locales";
import { useDispatch, useSelector } from "react-redux";
import { fetchWeatherApi } from "../features/fetchWeatherApi/weatherSlice";
import CircularProgress from "@mui/material/CircularProgress";

moment.locale("en");
export default function WeatherAppDetails() {
  const { t, i18n } = useTranslation();
  let [locale, setLocale] = useState("en");
  // let [weatherStatus, setWeatherStatus] = useState({});
  // let [name, setName] = useState();
  // let [temp, setTemp] = useState({});
  let [date, setDate] = useState("");
  let weather = useSelector((state) => state.weather.weatherObj);
  let isLoading = useSelector((state) => state.weather.isLoading);
  let dispatch = useDispatch();

  useEffect(() => {
    dispatch(fetchWeatherApi());
    setDate(moment().format("dddd l"));
  }, []);

  useEffect(() => {
    moment.locale(locale);
    setDate(moment().format("dddd l"));
    //lazem tstd3y setData tany 34an tktb 2l taree5 bs 2l mara deeh bel lo8a 2l tanya
    i18n.changeLanguage(locale);
  }, [locale]);

  function handleLocaleChange() {
    setLocale(locale === "en" ? "ar" : "en");
  }

  return (
    <Typography>
      <div
        style={{
          color: "white",
          border: "2px solid white",
          borderRadius: "10px",
          width: "300px",
          backgroundImage:
            "linear-gradient(to bottom right, #29b2dd 0%,  #33aadd 47%,  #2dc8ea 100%)",
        }}
      >
        <div
          style={{
            display: "flex",
            alignItems: "end",
            gap: "8px",
            margin: "20px 20px 10px 20px",
          }}
          dir={locale === "ar" ? "rtl" : "ltr"}
        >
          <h2 style={{ fontSize: "50px", lineHeight: "40px" }}>
            {t(weather.place)}
          </h2>
          <p style={{ margin: "0px 0px 0px 10px" }}>{date}</p>
        </div>
        <hr style={{ border: "1px solid white" }} />
        <div
          style={{
            margin: "10px 10px 10px 15px",
            display: "flex",
            justifyContent: "space-around",
            alignItems: "center",
          }}
          dir={locale === "ar" ? "rtl" : "ltr"}
        >
          <div style={{ textAlign: "center" }}>
            <h3 style={{ fontSize: "60px" }}>
              {isLoading ? (
                <CircularProgress sx={{ color: "white", marginRight: "2px" }} />
              ) : (
                ""
              )}
              {weather.weatherTemp}
              <sup style={{ fontSize: "20px" }}>°C</sup>
            </h3>
            <p>{t(weather.description)}</p>
            <p style={{ textWrap: "nowrap" }}>
              {t("min")} : {isLoading ? "~" : weather.minTemp} | {t("max")} :{" "}
              {isLoading ? "~" : weather.maxTemp}
            </p>
          </div>
          <div>
            <img src={weather.icon} style={{ width: "120px" }} alt="" />
          </div>
        </div>
        <div
          style={{
            display: "flex",
            justifyContent: "end",
            margin: "25px 30px",
          }}
          dir={locale == "ar" ? "rtl" : "ltr"}
        >
          <Button
            color="secondary"
            variant="contained"
            onClick={handleLocaleChange}
          >
            <Typography>{t("عربي")}</Typography>
          </Button>
        </div>
      </div>
    </Typography>
  );
}
